@font-face {
  font-family: 'Myriad Pro Regular';
  src: url('../fonts/MavenPro-Regular.ttf');
}

body {
  width: 100%;
  height: auto;
  background: #E4E5E7;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: #3A405B;
}

ahover {
  color: #3A405B;
}

aactive {
  color: #3A405B;
}

.wrapper {
  max-width: 1180px;
  padding-left: 100px;
  padding-right: 100px;
  margin: 0 auto;
}

/*SLIDER*/

.slide_block {
  width: 100%;
  margin: 0px 0 0 0;
  height: 500px;
  color: #fff;
  background: #1C1E23;
}

#perent_slider {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

#perent_slider #text_item_slider {
  width: 50%;
  height: 100%;
}

#perent_slider #img_item_slider {
  width: 50%;
  height: 100%;
}

#perent_slider #img_item_slider img {
  width: 100%;
  height: 100%;
}

#title_1 {
  font-family: 'MavenPro-Regular';
  font-size: 42px;
  text-align: center;
  margin-top: 88px;
}

#title_2 {
  margin: 30px;
}

#text_1 {
  text-align: left;
  margin: 35px 0px;
  font-family: 'MavenPro-Regular';
  font-size: 18px;
}

.button_slider {
  font-size: 16px;
  position: relative;
  cursor: pointer;
  font-family: "Myriad Pro Regular";
  color: #fff;
  padding: 15px 50px;
  background: #2754EB;
  border-radius: 30px;
}

#button_1 {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(16.666666666666668% - 32px);
  margin-left: calc(25% + 16px);
  margin-right: calc(25% + 16px);
  top: 50px;
}

#button_2 {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(25% - 32px);
  margin-left: calc(33.333333333333336% + 16px);
  margin-right: calc(33.333333333333336% + 16px);
  top: 130px;
}

.top_navigation {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(100% - 32px);
  margin-top: 50px;
  min-height: 55px;
  background: #fff;
  display: flex;
  line-height: 54px;
  flex-wrap: wrap;
  border-radius: 5px;
  justify-content: flex-start;
}

.top_navigation .sort_by {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(25% - 32px);
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-right: 2px solid #F2F2F2;
  padding-right: 20px;
}

.top_navigation .sort_by span {
  color: #a1a7bd;
  font-family: 'Myriad Pro Regular';
  font-size: 14px;
}

.top_navigation .sort_by select {
  width: 100px;
  border: none;
  color: #343e5c;
  font-family: 'Myriad Pro Regular';
  font-size: 14px;
}

.top_navigation .keywords {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(50% - 32px);
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-right: 2px solid #F2F2F2;
}

.top_navigation .keywords #keywords {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(25% - 32px);
