  font-size: 14px;
}

.top_navigation .keywords #keywords #input_keywords::-moz-placeholder {
  /* Firefox 19+ */
  color: #b9bfd3;
  font-family: 'Myriad Pro Regular';
  font-size: 14px;
}

.top_navigation .keywords #keywords #input_keywords:-ms-input-placeholder {
  color: #b9bfd3;
  font-family: 'Myriad Pro Regular';
  font-size: 14px;
}

.top_navigation .keywords #price {
