}

.top_navigation .keywords #price label {
  color: #a1a7bd;
  font-family: 'Myriad Pro Regular';
  font-size: 14px;
}

.top_navigation .keywords #price #input_prise {
  width: 65px;
  height: 33px;
  background: #E4E5E7;
  border: none;
  margin: 10px;
  font-family: 'Myriad Pro Regular';
  font-size: 14px;
  border-radius: 50px;
  padding-left: 10px;
}

.top_navigation .ship_to {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(25% - 32px);
  height: 100%;
  display: flex;
  justify-content: flex-end;
}

.top_navigation .ship_to span {
  color: #a1a7bd;
  font-family: 'Myriad Pro Regular';
  font-size: 14px;
  margin-right: 10px;
}

.top_navigation .ship_to select {
  width: 100px;
  border: none;
  color: #343e5c;
  font-family: 'Myriad Pro Regular';
  font-size: 14px;
}

.product {
  box-sizing: border-box;
