  color: #b8bfd3;
  margin-bottom: 15px;
}

.product .price_tovar {
  font-size: 16px;
  font-family: 'Myriad Pro Regular';
  color: #343E5C;
  margin-bottom: 15px;
}

.mobile_filter {
  width: 100%;
  margin-top: 30px;
  margin-bottom: 30px;
  padding-bottom: 15px;
  padding-top: 15px;
  background: #Fff;
  text-align: center;
  border-radius: 5px;
