}

.content {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(100% - 32px);
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 30px;
}

.content .left_panel {
  box-sizing: border-box;
  margin-left: 16px;
  margin-right: 16px;
  word-wrap: break-word;
  width: calc(25% - 32px);
  height: auto;
}

.content .left_panel .menu_panel {
  width: 100%;
  background: #fff;
  border-radius: 5px;
}

.content .left_panel .menu_panel .main_panel {
  width: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  cursor: pointer;
  justify-content: space-around;
  flex-wrap: wrap;
  border-bottom: 2px solid #E4E5E7;
}

.content .left_panel .menu_panel .main_panel h1 {
  font-family: 'Myriad Pro Regular';
  color: #343e5c;
  font-size: 15px;
}

.content .left_panel .menu_panel .main_panel span {
  font-family: 'Myriad Pro Regular';
  color: #b8bfd3;
  font-size: 15px;
}

.content .left_panel .menu_panel .secondary_panel {
  background: #f0f2f7;
  display: none;
  width: 100%;
}

.content .left_panel .menu_panel .secondary_panel ul li {
  font-family: 'Myriad Pro Regular';
  color: #a1a7bd;
  padding: 15px 20px;
  font-size: 15px;
  cursor: pointer;
  border-bottom: 2px solid #dfe4ee;
}

.content .left_panel .menu_panel .secondary_panel ul li:hover {
  color: #000;
}

.content .left_panel .available {
  width: 100%;
  height: auto;
  background: #fff;
  margin-top: 30px;
  margin-bottom: 30px;
  border-radius: 5px;
}

.content .left_panel .available #title_left_panel {
  width: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  cursor: pointer;
  padding-left: 45px;
  padding-right: 45px;
  border-bottom: 2px solid #E4E5E7;
}

.content .left_panel .available #title_left_panel h1 {
  font-family: 'Myriad Pro Regular';
  color: #343e5c;
  font-size: 15px;
}

.content .left_panel .available .Radiobuttons {
  width: 100%;
  height: auto;
  padding-top: 13px;
  padding-bottom: 13px;
}

.content .left_panel .available .Radiobuttons #radio_item {
  display: flex;
  flex-wrap: wrap;
  padding: 5px 40px;
  justify-content: space-between;
}

.content .left_panel .available .Radiobuttons #radio_item span {
  font-family: 'Myriad Pro Regular';
  color: #b8bfd3;
  font-size: 15px;
}

.content .left_panel .available .Radiobuttons #radio_item #radio_check {
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  justify-content: flex-start;
}

.content .left_panel .available .Radiobuttons #radio_item #radio_check label {
  font-family: 'Myriad Pro Regular';
  color: #343e5c;
  cursor: pointer;
  font-size: 15px;
  margin-left: 10px;
}

.content .left_panel .available .Radiobuttons #radio_item #radio_check input[type=radio] {
  position: absolute;
  visibility: hidden;
}

.content .left_panel .available .Radiobuttons #radio_item #radio_check .check {
  cursor: pointer;
  display: block;
  position: relative;
  border: 3px solid #AAAAAA;
  border-radius: 100%;
  height: 10px;
  width: 10px;
  z-index: 5;
  transition: border .25s linear;
  -webkit-transition: border .25s linear;
}

.content .left_panel .available .Radiobuttons #radio_item #radio_check .check::before {
  display: block;
  position: absolute;
  content: '';
  border-radius: 100%;
  height: 6px;
  width: 6px;
  top: 2px;
  left: 2px;
  margin: auto;
  transition: background 0.25s linear;
  -webkit-transition: background 0.25s linear;
}

.content .left_panel .available .Radiobuttons #radio_item #radio_check input[type=radio]:checked ~ .check {
  border: 3px solid #AAAAAA;
}

.content .left_panel .available .Radiobuttons #radio_item #radio_check input[type=radio]:checked ~ .check::before {
  background: #00deea;
}

.content .left_panel .available .Brands_left_panel {
  border-top: 2px solid #E4E5E7;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.content .left_panel .available .Brands_left_panel .see_all {
  color: #748af0;
  font-family: 'Myriad Pro Regular';
  font-size: 13px;
  margin-left: 100px;
  text-decoration: underline;
}

.content .left_panel .available .checkbuttons {
  width: 100%;
  height: auto;
  padding-top: 20px;
  padding-bottom: 10px;
}

.content .left_panel .available .checkbuttons .check_item {
  display: flex;
  cursor: pointer;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-left: 40px;
  padding-right: 40px;
  margin-bottom: 20px;
}

.content .left_panel .available .checkbuttons .check_item #number {
  font-family: 'Myriad Pro Regular';
  color: #b8bfd3;
  font-size: 15px;
  margin-top: 7px;
}

.content .left_panel .available .checkbuttons .check_item .label {
  display: flex;
}

.content .left_panel .available .checkbuttons .check_item .label #name_brand {
  color: #343e5c;
  margin-top: 5px;
  margin-left: 15px;
  font-family: 'Myriad Pro Regular';
  font-size: 18px;
}

.content .left_panel .available .checkbuttons .check_item .label .label__checkbox {
  display: none;
}

.content .left_panel .available .checkbuttons .check_item .label .label__check {
  display: inline-block;
  border-radius: 5px;
  border: 5px solid rgba(0, 0, 0, 0.1);
  background: white;
  vertical-align: middle;
  width: 18px;
  height: 18px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: border .3s ease;
}

.content .left_panel .available .checkbuttons .check_item .label .label__check i.icon {
  opacity: 0.2;
  font-size: calc(0.1rem + 1vw);
  color: transparent;
  transition: opacity .3s .1s ease;
  -webkit-text-stroke: 3px rgba(0, 0, 0, 0.5);
}

.content .left_panel .available .checkbuttons .check_item .label .label__check:hover {
  border: 5px solid rgba(0, 0, 0, 0.2);
}

.content .left_panel .available .checkbuttons .check_item .label .label__checkbox:checked + .label__text .label__check {
  animation: check 0.5s cubic-bezier(0.895, 0.03, 0.685, 0.22) forwards;
}

.content .left_panel .available .checkbuttons .check_item .label .label__checkbox:checked + .label__text .label__check .icon {
  opacity: 1;
  transform: scale(0);
  color: white;
  -webkit-text-stroke: 0;
  animation: icon 0.3s cubic-bezier(1, 0.008, 0.565, 1.65) 0.1s 1 forwards;
}

@keyframes icon {
  from {
    opacity: 0;
    transform: scale(0.3);
  }

  to {
    opacity: 1;
    transform: scale(0.9);
  }
}

@keyframes check {
  0% {
    width: 1em;
    height: 1em;
    border-width: 5px;
  }

  50% {
    width: 1.5em;
    height: 1.5em;
    opacity: 0.1;
    background: rgba(0, 0, 0, 0.2);
    border-width: 15px;
  }

  12% {
    width: 1.5em;
    height: 1.5em;
    opacity: 0.4;
    background: rgba(0, 0, 0, 0.1);
    border-width: 0;
  }

  50% {
    width: 1.6em;
    height: 1.6em;
    background: #00d478;
    border: 0;
    opacity: 0.6;
  }

  100% {
    width: 1.7em;
    height: 1.7em;
    background: #00d478;
    border: 0;
    opacity: 1;
  }
}

.content .left_panel .available .Condition_left_panel {
  border-top: 2px solid #E4E5E7;
